import { Axios } from 'axios'
import {
  VaultBlock,
  VaultBlockData,
  VaultBlockRoutes,
  VaultBlocksClientModel,
  VaultBlocksSearchQuery,
} from '../vault-block.model'
import { ApiEntity } from '../../core'

export class VaultBlocksClient
  extends ApiEntity
  implements VaultBlocksClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a vault block
   * @param body - the vault block data
   * @returns the promise for create a new vault block
   */
  public create(body: VaultBlockData) {
    return this._create<VaultBlock>(VaultBlockRoutes.v1Create, body)
  }

  /**
   * Search blocks by params
   * @param searchParams - the vault blocks search params
   * @returns the promise for search blocks
   */
  public search(searchParams?: VaultBlocksSearchQuery) {
    return this._search<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }

  /**
   * Search all blocks by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: VaultBlocksSearchQuery) {
    return this._searchAll<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }

  /**
   * List epochs by params
   * @param searchParams - the search vault epochs search params
   * @returns the promise for list epochs
   */
  public list(searchParams?: VaultBlocksSearchQuery) {
    return this._list<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }

  /**
   * List all epochs by params
   * @param searchParams - the search vault epochs search params
   * @returns the promise for list epochs
   */
  public listAll(searchParams?: VaultBlocksSearchQuery) {
    return this._listAll<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }

  /**
   * Find an epoch by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public findOne(searchParams?: VaultBlocksSearchQuery) {
    return this._findOne<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }
  /**
   * Find an epoch by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public readOne(searchParams?: VaultBlocksSearchQuery) {
    return this._readOne<VaultBlock>(VaultBlockRoutes.v1Search, searchParams)
  }
}
